html,
body,
#root {
  height: 100%;
  margin: 0;
  color: white;
}
$pageWidth: 400px;
$pageHeight: 250px;
main {
  height: 100%;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .header {
    display: flex;
    justify-content: end;
    font-size: x-large;
    width: $pageWidth;
  }
  .pageListWrap {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: $pageWidth * 2.5;
    height: $pageHeight + 100;
    // max-width: 900px;
    // margin: 40px auto;
    .pageListBg {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      .pageListShadow {
        box-shadow: 0 0 50px rgba(255, 255, 255, 0.374);
        width: 50%;
        height: $pageHeight;
        transform: scaleY(0.7);
        border-radius: 100px / 10px;
      }
    }
    .pageList {
      background-color: #000000;
      position: relative;
      height: $pageHeight;
      // display: flex;
      width: $pageWidth;
      // .pageWrap:first-child {
      //   transform: scale(0.6);
      // }
      // .pageWrap:last-child {
      //   transform: scale(0.6);
      // }
      @keyframes imgFocus {
        from {
          filter: brightness(50%);
        }
        to {
          filter: brightness(100%);
        }
      }
      @keyframes imgUnfocus {
        from {
          filter: brightness(100%);
        }
        to {
          filter: brightness(50%);
        }
      }

      .center {
        z-index: 4;
        // animation: imgFocus 0.5s forwards;
      }
      .side {
        z-index: 3;
        // animation: imgUnfocus 0.5s forwards;
      }
      .sideside {
        z-index: 2;
      }
      @keyframes imgHover {
        from {
          box-shadow: 0 0 30px rgba(255, 255, 255, 0.8);
        }
        to {
          box-shadow: 0 0 40px rgba(255, 255, 255, 0.8);
        }
      }
      @keyframes imgUnhover {
        from {
          box-shadow: 0 0 40px rgba(255, 255, 255, 0.8);
        }
        to {
          box-shadow: 0 0 30px rgba(255, 255, 255, 0.8);
        }
      }
      .pageWrap:hover {
        cursor: pointer;
      }
      .pageWrap:hover .pageBg {
        animation: imgHover 0.3s forwards;
      }
      @keyframes titleAppear {
        from {
          top: $pageHeight - 50;
        }
        to {
          top: $pageHeight + 10;
        }
      }
      @keyframes titleDisappear {
        from {
          top: $pageHeight + 10;
        }
        to {
          top: $pageHeight - 50;
        }
      }
      .pageWrap:hover .title {
        animation: titleAppear 0.5s forwards;
      }
      .pageWrap {
        position: absolute;
        height: $pageHeight;
        // padding-bottom: 50px;
        top: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        .title {
          position: absolute;
          animation: titleDisappear 0.5s forwards;
          font-size: x-large;
        }
        .page {
          width: $pageWidth;
          height: $pageHeight;
          background-color: rgb(18, 18, 18);
          position: relative;
          box-shadow: 0 1px 4px rgba(255, 255, 255, 0.3),
            0 0 40px rgba(255, 255, 255, 0.1) inset;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 10px;
          overflow: hidden;
          .img {
            object-fit: cover;
            width: 100%;
            height: 100%;
          }
        }
        .pageBg {
          position: absolute;
          // box-shadow: 0 0 30px rgba(255, 255, 255, 0.8);
          top: 20px;
          bottom: 20px;
          left: 0;
          right: 0;
          // height: $pageHeight - 30;
          border-radius: 100px / 10px;
          animation: imgUnhover 0.3s forwards;
        }
      }
    }
  }
}
